var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',[_c('h1',[_vm._v("About Me")]),_c('figure',{attrs:{"id":"figure-pilot"}},[_c('img',{attrs:{"id":"img-pilot","src":require("../assets/steve_pilot.png")}})]),_c('p',[_vm._v("Welcome to my personal portfolio site. My name is Steve Burns. I was neither the host of Blues Clues in the 1990s nor am I a distant relative of Montgomery J Burns of The Simpsons.  I "),_c('strong',[_c('em',[_vm._v("am")])]),_vm._v(" a U.S. Air Force veteran transitioning to a second career in software development. As a graduate of the Air Force Academy and former combat pilot, I am seeking an equally challenging second career.")]),_c('p',[_vm._v("The path that landed me here spans almost 40 years. A fascination with BASIC on the Atari 800 started it all during my 1980s childhood. I was hooked. Over the years I have tried my hand at many ways of drawing pixels on screens. Along the way I discovered you could make those pixels meaningful with data. The internet changed everything by allowing most everyone to share those meaningful pixels. But this hobby often overlapped with my Air Force career and because of the military commitment, writing computer programs remained a hobby.")]),_c('figure',{attrs:{"id":"figure-dev"}},[_c('img',{attrs:{"id":"img-dev","src":require("../assets/steve-dev.jpg")}})]),_c('p',[_vm._v("Many military members leave the armed forces unsure of what they want to do upon re-entering the civilian world and my story was no exception. I was 100% certain about one thing: just like flying jets, I need to find a job that doesn't feel like a job. My excitement for computers and software had all the potential to be that second dream job but I needed formal training and projects to gain experience. After months of researching my options, I chose the immersive web development course at Galvanize in Boulder, Colorado to connect the dots to a new career. As challenging as anything I accomplished in the military, Galvanize was the smartest professional decision I had made in 25 years.")]),_c('p',[_vm._v("This website is a virtual online hub to my projects, both big and small, demonstrating the skills I've learned over the years in software development.  Writing fast, clean, and efficient code never feels like work to me. It is always a challenge to be sure but my military experiences demand a challenge. I want to build things that work and I embrace the obstacles along the way. The whole process is just as rewarding as being a military pilot.  It is work that doesn't feel like work.")])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('section',[_c('h1',[_vm._v("About This Site")]),_c('p',[_vm._v("Here's some fun facts about building this site...")]),_c('p',[_vm._v("The modern Single Page Application (SPA) curriculum of my bootcamp experience at Galvanize was exclusively tailored to React but learning other programming approaches was highly encouraged. After wrapping my head around the broad abilities of React, I was only aware that similar frameworks and libraries existed and so I began to branch out a little further than what I learned at Galvanize.")]),_c('p',[_vm._v("On my first shallow dive into Vue I was immediately hooked.  React is arguably the industry's popular front end approach to modern web apps and most of my projects are React-centric.  But this portfolio site was to be the core of all my programming work and I wanted it to stand apart.  Wading into completely uncharted waters, I made Vue the backbone of this site.")]),_c('p',[_vm._v("Building off the core I also experimented with D3js (the Skills page interactive chart) and Greensock Animation (the Home page animated logos).")]),_c('p',[_vm._v("Every project needs some Font Awesome... because it's awesome.")]),_c('p',[_vm._v("Finally, I am versed in Boostrap and Material Design for styling, but I tend to write my own styles in 